/* eslint-disable react/no-unused-prop-types */
import { Icon } from 'components/atoms/icon/icon';
import { SecondaryButton } from 'components/molecues/button';
import { ThemeSvgGradient } from 'components/molecues/theme-svg-gradient/theme-svg-gradient';
import { ToggleGroup } from 'components/molecues/toggle-group/toggle-group';
import { EquipmentCategory } from 'domain/equipment/equipment-category';
import { EquipmentComponent } from 'domain/equipment/equipment-component';
import { EquipmentSet } from 'domain/equipment/equipment-set';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React, { useEffect, useReducer, useState } from 'react';
import { numberToPriceFormat } from 'utils/number-to-price-format';

interface EquipmentEditItemProps {
  item: EquipmentComponent<IGatsbyImageData>,
  refPrice: number,
}

const EquipmentEditItem = ({ item, refPrice }: EquipmentEditItemProps) => (
  <div
    className="group-data-[state=on]:border-gradient group relative flex h-24 items-center gap-4 rounded-lg p-4 text-main-white group-data-[state=on]:border group-data-[state=on]:bg-theme-gradient-background-dark"
  >
    <div className="h-fit w-fit">
      <GatsbyImage image={item.avatarFile} alt="Image of a component" className="h-full w-full" />
    </div>
    <div className="flex w-full flex-col items-start gap-2">
      <span className="text-sm">{item.name}</span>
      <span className="font-uni-sans text-base font-black italic">
        {`${numberToPriceFormat(item.price - refPrice, true)} ZŁ`}
      </span>
      <span className="absolute right-0 bottom-0 flex gap-2 pb-4 pr-3 text-sm font-semibold tracking-tighter text-secondary-green group-data-[state=off]:hidden">
        <div className="flex h-4 w-4 items-center justify-center rounded-full bg-secondary-green fill-secondary-green p-1">
          <Icon name="Checkmark" className="rounded-full stroke-background-dark" />
        </div>
        wybrany
      </span>
    </div>
  </div>
);

type Action = { type: 'replace', component: EquipmentComponent<IGatsbyImageData> }
const equipmentSetReducer: React.Reducer<
  EquipmentSet<IGatsbyImageData>,
  Action
> = (state, { type, component }) => {
  switch (type) {
    case 'replace':
      return {
        ...state,
        [component.category]: component,
      };
    default:
      return state;
  }
};

interface EditEquipmentPanelProps {
  currentEquipmentSet: EquipmentSet<IGatsbyImageData>,
  referenceEquipmentSet: EquipmentComponent<IGatsbyImageData>[],
  onUnacceptedChange: (newSet: EquipmentSet<IGatsbyImageData>) => void,
  onAcceptedChange: (newSet: EquipmentSet<IGatsbyImageData>) => void;
}

export const EditEquipmentPanel = (props: EditEquipmentPanelProps) => {
  const {
    currentEquipmentSet,
    referenceEquipmentSet,
    onAcceptedChange,
    onUnacceptedChange,
  } = props;
  const [
    currentComponentSelection,
    setCurrentComponentSelection,
  ] = useState<EquipmentCategory>(EquipmentCategory.GPU);

  const [temporaryComponentSet, dispatch] = useReducer(equipmentSetReducer, currentEquipmentSet);

  const handleAccept = () => {
    if (onAcceptedChange) onAcceptedChange(temporaryComponentSet);
  };

  useEffect(() => {
    onUnacceptedChange(temporaryComponentSet);
  }, [temporaryComponentSet]);

  const handleChange = (
    category: EquipmentCategory,
    id: string,
  ) => {
    const newComponent = referenceEquipmentSet.find((item) => item.priceId === id);
    if (newComponent) dispatch({ type: 'replace', component: newComponent });
  };

  return (
    <div className="flex flex-col gap-4">
      <div className="border-gradient flex w-full flex-col gap-4 rounded-2xl bg-background-light p-4">
        <div className="w-full overflow-auto">
          <ToggleGroup
            className="h-12 w-full justify-between border-b border-secondary-gray uppercase"
            value={currentComponentSelection}
            onChanged={setCurrentComponentSelection}
            items={
                [...new Set(referenceEquipmentSet.map((item) => item.category))]
                  .map((category) => ({
                    id: category,
                    element: (
                      <div className="flex w-full items-center justify-center gap-2 p-2">
                        <Icon name="Equipment" className="h-6 w-6 fill-secondary-gray group-data-[state=on]:fill-[url(#equipment-icon)]"><ThemeSvgGradient id="equipment-icon" /></Icon>
                        <div className="h-full text-sm font-extrabold uppercase text-main-white">{category}</div>
                      </div>
                    ),
                    className: 'w-full flex items-center bg-theme-gradient-background-light',
                  }))
              }
          />
        </div>
        <ToggleGroup
          className="flex flex-col"
          value={temporaryComponentSet[currentComponentSelection].priceId}
          onChanged={(id) => handleChange(currentComponentSelection, id)}
          items={
            [...referenceEquipmentSet]
              .sort((a, b) => (a.price > b.price ? 1 : -1))
              .filter(({ category }) => category === currentComponentSelection)
              .map((item) => (
                ({
                  id: item.priceId,
                  element: (
                    <EquipmentEditItem
                      item={item}
                      refPrice={currentEquipmentSet[currentComponentSelection].price}
                    />
                  ),
                })
              ))
          }
        />
      </div>
      <div className="flex w-full flex-col items-center justify-between gap-4 rounded-2xl md:flex-row md:bg-background-light md:p-6">
        <div className="flex w-full justify-between">
          <span className="visible md:hidden">Do zapłaty</span>
          <span className="flex items-baseline whitespace-nowrap font-uni-sans font-black">
            <span className="text-2xl">
              {numberToPriceFormat(
                Object.entries(temporaryComponentSet)
                  .reduce((sum, item) => sum + item[1].price, 0),
              )}
            </span>
            <span className="ml-1 text-lg uppercase">zł</span>
          </span>
        </div>
        <SecondaryButton className="w-full md:w-fit" onClick={handleAccept}>
          zapisz zmiany
        </SecondaryButton>
      </div>
    </div>
  );
};
