/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';

interface VideoProps extends React.IframeHTMLAttributes<HTMLIFrameElement> {
  videoSrcURL?: string,
  videoTitle?: string,
}

export const Video = ({ videoSrcURL, videoTitle, ...restProps }: VideoProps) => (
  <iframe
    src={videoSrcURL}
    title={videoTitle}
    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture; clipboard-write; web-share"
    frameBorder="0"
    allowFullScreen
    {...restProps}
  />
);
