import { IEquipmentDTO } from 'domain/equipment/equipment-dto';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React, {
  useCallback, useMemo, useState,
} from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { EquipmentList } from 'components/molecues/equipment-list/equipment-list';
import { EquipmentSet } from 'domain/equipment/equipment-set';
import { useCart } from 'infrastructure/equipment/use-cart';
import { Dialog } from 'components/molecues/dialog/dialog';
import { PrimaryButton, SecondaryButton } from 'components/molecues/button';
import { Link } from 'gatsby';
import { uid } from 'utils/uid';
import { CartItemDTO, MetaDataKeys, ItemType } from 'domain/equipment/cart';
import { GalleryData } from 'infrastructure/equipment/convert-to-gallery-data';
import { calculatePerformanceCoefficient } from 'domain/equipment/calculate-performance-coefficient';
import { Video } from 'components/atoms/video/video';
import { EditEquipmentPanel } from '../edit-equipment-panel/edit-equipment-panel';
import { EquipmentSetPerformanceGallery } from '../equipment-performance-gallery/equipment-performance-gallery';

interface EquipmentSetContentProps {
  equipment: IEquipmentDTO<IGatsbyImageData>,
  galleryData: GalleryData,
}

export const EquipmentSetContent = (props: EquipmentSetContentProps) => {
  const { equipment, galleryData } = props;
  const [isEditMode, setIsEditMode] = useState(false);
  const [isInfoDialogVisible, setIsinfoDialogVisible] = useState(false);
  const [performanceCoefficient, setPerformanceCoefficient] = useState(1);

  const { addToCart } = useCart();

  const defaultComponents = useMemo(() => (
    equipment.compatibleComponents.filter((component) => component.default)
  ), [equipment]);

  const [currentSet, setCurrentSet] = useState<EquipmentSet<IGatsbyImageData>>(
    defaultComponents.reduce((obj, component) => (
      { ...obj, [component.category]: component }
    ), {} as EquipmentSet<IGatsbyImageData>),
  );

  const handleUnacceptedEdit = useCallback((newSet: EquipmentSet<IGatsbyImageData>) => {
    if (isEditMode) {
      setPerformanceCoefficient(calculatePerformanceCoefficient(newSet));
    }
  }, [isEditMode]);

  const handleAccept = (newSet: EquipmentSet<IGatsbyImageData>) => {
    setCurrentSet(newSet);
    setIsEditMode(false);
  };

  const handleAddToCart = useCallback(() => {
    const buildId = `${equipment.buildId}_${uid()}`;
    const items: CartItemDTO<IGatsbyImageData>[] = Object.entries(currentSet).map(
      ([, { name, price, priceId }]) => ({
        id: uid(),
        groupTags: {
          [MetaDataKeys.TYPE]: ItemType.EQUIPMENT,
          [MetaDataKeys.BUILD_ID]: equipment.buildId,
          [MetaDataKeys.BUILD_CART_ID]: buildId,
          [MetaDataKeys.COVER_IMAGE]: equipment.images[0].imgFile,
        },
        price,
        priceId,
        name,
      }),
    );
    addToCart(items, true);
    setIsinfoDialogVisible(true);
  }, [currentSet]);

  const closeInfoDialog = useCallback(() => {
    setIsinfoDialogVisible(false);
  }, []);

  /** @todo refactor carousel */
  const tempImage = (
    <div className="flex h-full w-full flex-col gap-4">
      <Carousel
        showThumbs
        showStatus={false}
        className="h-full w-full"
        renderThumbs={(children) => children.map((child) => <div key={Math.random()} className="h-20 w-20 rounded-md">{child}</div>)}
      >
        {equipment.images.map((imageData, i) => (
          // eslint-disable-next-line react/no-array-index-key
          <GatsbyImage key={`${imageData.name}-${i}`} image={imageData.imgFile} alt="" className="h-full rounded-2xl" />
        ))}
      </Carousel>
    </div>
  );

  return (
    <div className="flex h-full w-full flex-col gap-6 bg-background-dark py-32 px-8 md:px-24 lg:px-32">
      <div>
        <Dialog open={isInfoDialogVisible} className="flex h-full w-full" overlayClassName="opacity-100">
          <div className="flex w-full flex-col items-center justify-center gap-4 bg-background-light p-4 text-main-white md:p-8">
            <h2 className="text-center font-uni-sans font-black uppercase">Produkt dodano do koszyka</h2>
            <h3 className="text-center text-sm">{Object.entries(currentSet).map(([, value]) => value.name).join(' / ')}</h3>
            <GatsbyImage className="max-w-xs rounded-xl" image={equipment.images.find((image) => image.featured)!.imgFile} alt="Equipment cover" />
            <div className="flex h-fit flex-col gap-4 md:flex-row">
              <SecondaryButton onClick={closeInfoDialog}>Wróć do zakupów</SecondaryButton>
              <Link to="/koszyk"><PrimaryButton onClick={closeInfoDialog}>Przejdź do koszyka</PrimaryButton></Link>
            </div>
          </div>
        </Dialog>
        <div className="flex w-full gap-4">
          <div className="hidden w-full md:flex">{tempImage}</div>
          <div className="flex w-full flex-col gap-4">
            <div className="order-1 flex flex-col text-center md:order-2 md:text-start">
              <h1 className="whitespace-nowrap font-uni-sans text-3xl font-black uppercase italic">
                mamy to!
              </h1>
              <h2 className="py-6">
                Tak się prezentuje sprzęt stworzony specjalnie dla Ciebie
              </h2>
            </div>
            <div className="order-4 md:order-3">
              {isEditMode ? (
                <EditEquipmentPanel
                  currentEquipmentSet={currentSet}
                  referenceEquipmentSet={equipment.compatibleComponents}
                  onAcceptedChange={handleAccept}
                  onUnacceptedChange={handleUnacceptedEdit}
                />
              ) : (
                <EquipmentList
                  equipmentSet={currentSet}
                  onModifyEquipment={() => setIsEditMode(true)}
                  onAddToCart={handleAddToCart}
                />
              )}
            </div>
            <div className="order-2 flex md:hidden">{tempImage}</div>
          </div>
        </div>
      </div>
      <section>
        <EquipmentSetPerformanceGallery
          galleryData={galleryData}
          performanceCoefficient={performanceCoefficient}
        />
      </section>
      <section className="h-fit">
        <div className="aspect-[4/3] w-full">
          <Video
            videoSrcURL={equipment.videos[0]?.url!}
            videoTitle={equipment.videos[0]?.name!}
            className="h-full w-full"
          />
        </div>
      </section>
    </div>
  );
};
