import { Icon } from 'components/atoms/icon/icon';
import { EquipmentSet } from 'domain/equipment/equipment-set';
import { IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';
import { PrimaryButton } from '../button';
import { EquipmentItem } from '../equipment-item/equipment-item';
import { PriceBox } from '../price-box/price-box';

interface EquipmentListProps {
  equipmentSet: EquipmentSet<IGatsbyImageData>;
  onModifyEquipment: () => void;
  onAddToCart: () => void;
}

export const EquipmentList = ({
  equipmentSet,
  onModifyEquipment,
  onAddToCart,
}: EquipmentListProps) => (
  <div className="flex flex-col gap-4">
    <div className="border-gradient order-2 flex w-full flex-col gap-4 rounded-2xl border bg-theme-gradient-background-dark p-4 md:order-1">
      <button
        type="button"
        onClick={onModifyEquipment}
        className="flex h-12 w-full items-center justify-end gap-2 border-b border-b-gray-600 font-extrabold uppercase"
      >
        <Icon name="Equipment" className="h-6 w-6 fill-secondary-gray" />
        modyfikuj
      </button>
      <div className="grid w-full grid-cols-1 gap-4 md:grid-cols-2 [&>*]:col-span-1">
        {Object.entries(equipmentSet).map(([, item]) => (
          <EquipmentItem key={item.priceId} item={item} />
        ))}
      </div>
    </div>
    <div className="order-1 flex w-full flex-col items-center justify-between gap-4 rounded-2xl md:order-2 md:flex-row md:bg-background-light md:p-6">
      <PriceBox
        className="w-full"
        price={Object.entries(equipmentSet)
          .reduce((sum, [, item]) => sum + item.price, 0)}
      />
      <PrimaryButton className="w-full md:w-fit" onClick={onAddToCart}>
        dodaj do koszyka
      </PrimaryButton>
    </div>
  </div>
);
