import { IGatsbyImageData } from 'gatsby-plugin-image';
/* eslint-disable no-underscore-dangle */
import { BenchmarkGame } from 'domain/equipment/benchmark-game';
import { EquipmentComponent } from 'domain/equipment/equipment-component';
import { IEquipmentDTO } from 'domain/equipment/equipment-dto';
import { GamesPerfomance } from 'domain/equipment/games-performance';
import { Processor } from 'domain/equipment/processor';
import { Resolution } from 'domain/equipment/resolution';
import { EquipmentImage } from 'domain/equipment/equipment-image';
import { EquipmentVideo } from '../../domain/equipment/equipment-video';

export class EquipmentDTO implements IEquipmentDTO<IGatsbyImageData> {
  buildId: string;

  priceCategory: string;

  platform: Processor;

  description: string;

  name: string;

  compatibleComponents: EquipmentComponent<IGatsbyImageData>[];

  performance: GamesPerfomance<IGatsbyImageData>;

  images: EquipmentImage<IGatsbyImageData>[];

  videos: readonly EquipmentVideo[];

  constructor(queryData: Queries.equipmentQueryQuery) {
    const { equipmentSetsJson: data } = queryData;
    this.priceCategory = data?.priceCategoryString!;
    this.platform = data?.platform as unknown as Processor;
    this.name = data?.name!;
    this.description = data?.description!;
    this.buildId = data?.buildId!;
    this.videos = data?.videos!;
    this.compatibleComponents = data?.components?.map((item) => ({
      ...item,
      avatarFile: item?.avatarFile?.childImageSharp?.gatsbyImageData,
    })) as EquipmentComponent<IGatsbyImageData>[];

    this.performance = {
      [Resolution.FHD]: data?.performance?._1080p!.map((item) => ({
        ...item,
        imgFile: item?.imgFile?.childImageSharp?.gatsbyImageData,
      })) as BenchmarkGame<IGatsbyImageData>[],
      [Resolution.QHD]: data?.performance?._1440p!.map((item) => ({
        ...item,
        imgFile: item?.imgFile?.childImageSharp?.gatsbyImageData,
      })) as BenchmarkGame<IGatsbyImageData>[],
      [Resolution.UHD]: data?.performance?._4k!.map((item) => ({
        ...item,
        imgFile: item?.imgFile?.childImageSharp?.gatsbyImageData,
      })) as BenchmarkGame<IGatsbyImageData>[],
    };
    this.images = data?.images?.map((item) => ({
      ...item,
      imgFile: item?.imgFile?.childImageSharp?.gatsbyImageData,
    })) as EquipmentImage<IGatsbyImageData>[];
  }
}
