import classNames from 'classnames';
import { ResolutionToggleGroup } from 'components/molecues/resolution-toggle-group/resolution-toggle-group';
import { Resolution } from 'domain/equipment/resolution';
import { GalleryData } from 'infrastructure/equipment/convert-to-gallery-data';
import React, { useMemo, useState } from 'react';
import { GamesFpsGallery } from '../games-fps-gallery/games-fps-gallery';

interface EquipmentSetPerformanceGalleryProps {
  performanceCoefficient: number,
  galleryData: GalleryData,
}

export const EquipmentSetPerformanceGallery = (props: EquipmentSetPerformanceGalleryProps) => {
  const { performanceCoefficient = 1, galleryData } = props;
  const defaultItemsCount = 7;
  const [showMore, setShowMore] = useState(false);

  const [resolution, setResolution] = useState(Resolution.FHD);

  const filteredData = useMemo(() => (
    galleryData
      .filter((item) => item.resolution === resolution)
      .map((item) => ({ ...item, fps: Math.round(item.fps * performanceCoefficient) }))
  ), [resolution, performanceCoefficient]);

  return (
    <div className="flex h-full flex-col items-center gap-4 p-6">
      <h2 className="text-center font-uni-sans font-black uppercase">
        szacowana wydajność
      </h2>
      <div className="flex w-min justify-center ">
        <ResolutionToggleGroup
          value={resolution}
          onChange={setResolution}
          className="bg-theme-gradient-background-dark"
        />
      </div>
      <div className="group flex h-max w-fit flex-col flex-nowrap">
        <GamesFpsGallery
          data-show-more={showMore ? 'true' : 'false'}
          // eslint-disable-next-line tailwindcss/no-custom-classname
          className={classNames(
            'data-[show-more=false]:hide-elements overflow-visible',
            { 'md:flex': filteredData.length <= defaultItemsCount },
            { 'md:grid-cols-7': filteredData.length > defaultItemsCount },
          )}
          fpsData={filteredData}
        />
        {galleryData.length > defaultItemsCount && !showMore ? (
          <button
            type="button"
            aria-label="Pokaż więcej gier"
            onClick={() => setShowMore(true)}
          >
            Pokaż więcej gier
          </button>
        ) : (
          <button
            type="button"
            aria-label="Pokaż mniej gier"
            onClick={() => setShowMore(false)}
          >
            Pokaż mniej gier
          </button>
        )}
      </div>
    </div>
  );
};
