/* eslint-disable no-underscore-dangle */
import React from 'react';
import { graphql, HeadFC, PageProps } from 'gatsby';
import { MainTemplate } from 'components/templates/main-template';
import { Navbar } from 'components/organisms/navbar/navbar';
import { EquipmentSetContent } from 'components/organisms/equipment-set-content/equipment-set-content';
import { EquipmentDTO } from 'infrastructure/equipment/equipment-set-dto';
import { convertToGalleryData } from 'infrastructure/equipment/convert-to-gallery-data';
import { Processor } from 'domain/equipment/processor';
import { Footer } from 'components/organisms/footer/footer';

const SetPage = (props: PageProps<Queries.equipmentQueryQuery>) => {
  const { data } = props;
  const equipmentSet = new EquipmentDTO(data);
  const galleryData = convertToGalleryData(
    data.equipmentSetsJson?.performance!,
    data.equipmentSetsJson?.platform as Processor,
    data.equipmentSetsJson?.priceCategoryString || '',
  );
  return (
    <MainTemplate
      header={<Navbar />}
      content={<EquipmentSetContent equipment={equipmentSet} galleryData={galleryData} />}
      className="bg-background-dark text-main-white"
      footer={<Footer />}
    />
  );
};

export const Head: HeadFC = () => <title>Skomponuj swój sprzęt</title>;

export default SetPage;

export const query = graphql`
  query equipmentQuery ($id: String) {
     equipmentSetsJson(id: {eq: $id}) {
      priceCategoryString
    platform
    buildId
    videos {
        name
        featured
        url
      }
    performance {
      _1080p {
        fps
        imgUrl
        name
        imgFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      _1440p {
        name
        imgUrl
        fps
        imgFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      _4k {
        name
        imgUrl
        fps
        imgFile {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
    name
    description
    videos {
      featured
      name
      url
    }
    components {
      category
      default
      name
      performance
      price
      priceId
      avatarFile {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
    images {
      featured
      name
      imgUrl
      imgFile {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
   }
  }
`;
