import { EquipmentComponent } from 'domain/equipment/equipment-component';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';

interface EquipmentItemProps {
  item: EquipmentComponent<IGatsbyImageData>,
}

export const EquipmentItem = ({ item: { category, name, avatarFile } }: EquipmentItemProps) => (
  <div className="flex w-full gap-4 overflow-hidden">
    <div className="h-full">
      {avatarFile && <GatsbyImage image={avatarFile} alt={`${category} avatar`} className="h-16 w-16" />}
    </div>
    <div className="flex h-full flex-col justify-center overflow-hidden text-base">
      <div className="font-uni-sans text-base font-black italic">
        {category}
      </div>
      <div className="overflow-hidden text-ellipsis whitespace-nowrap text-xs font-normal md:text-sm">
        {name}
      </div>
    </div>
  </div>
);
